// media queries
$onlyDesktop: 'screen and (min-width: 1200px)' !default;
$onlyLaptop: 'screen and (min-width: 992px)' !default;
$onlyTablet: 'screen and (min-width: 768px)' !default;
$onlyPhone: 'screen and (min-width: 576px)' !default;

$desktop: 'screen and (max-width: 1679px)' !default;
$smallDesktop: 'screen and (max-width: 1199px)' !default;
$laptop: 'screen and (max-width: 991px)' !default;
$tablet: 'screen and (max-width: 767px)' !default;
$phone: 'screen and (max-width: 575px)' !default;

//grid
$grid-columns: 12 !default;
$gutter-width: 40px !default;
$grid-gutter-width: 20px !default;
$grid-gutter-width-xs: 60px !default;
$grid-gutter-width-md: 80px !default;

//Container sizes
// Small screen / tablet
$container-tablet: (720px + $grid-gutter-width-xs) !default;
//** For `$screen-sm-min` and up.
$container-sm: $container-tablet !default;
// Medium screen / desktop
$container-desktop: (940px + $grid-gutter-width-md) !default;
//** For `$screen-md-min` and up.
$container-md: $container-desktop !default;
// Large screen / wide desktop
$container-large-desktop: (1252px + $grid-gutter-width) !default;
//** For `$screen-lg-min` and up.
$container-lg: $container-large-desktop !default;

//== fonts
$font-path: "../fonts/" !default;
$font-OpensSans-Semibold: "OpenSans-Semibold" !default;
$font-ProximaNova-Sbold: "ProximaNova-Sbold" !default;
$font-ProximaNova-Thin: "ProximaNova-Thin" !default;
$font-ProximaNova-Light: "ProximaNova-Light" !default;
$font-ProximaNova-Reg: "ProximaNova-Reg" !default;
$font-ProximaNova-Bold: "ProximaNova-Bold" !default;

//== Typography
$font-size-base: 14px !default;

//== Sprites svg
$mq-base-font-size: 14px !default;
